"use client";

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import Image from "next/image";
import { 
  ArrowLeft, 
  Eye, 
  EyeOff,
  Facebook,
  Mail,
  Lock,
  User,
  Building2,
  Users,
  GraduationCap
} from "lucide-react";
import { useState } from "react";
import Link from "next/link";
import toast from "react-hot-toast";
import { authApi, LoginCredentials } from "@/lib/api";

export default function LoginPage() {
  const [showPassword, setShowPassword] = useState(false);
  const [userType, setUserType] = useState<"student" | "faculty" | "admin">("student");
  const [isLoading, setIsLoading] = useState(false);
  
  // Form states
  const [loginEmail, setLoginEmail] = useState("");
  const [loginPassword, setLoginPassword] = useState("");
  
  // Register form states
  const [registerData, setRegisterData] = useState({
    first_name: "",
    last_name: "",
    email: "",
    password: "",
    confirm_password: "",
    phone: "",
  });

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    
    try {
      const credentials: LoginCredentials = {
        email: loginEmail,
        password: loginPassword,
        role: userType,
      };
      
      const response = await authApi.login(credentials);
      
      // Store token
      localStorage.setItem('token', response.token);
      localStorage.setItem('user', JSON.stringify(response.user));
      localStorage.setItem('role', response.role);
      
      toast.success(`Welcome back, ${response.user.first_name}!`);
      
      // Redirect based on role
      const routes = {
        student: "/student",
        faculty: "/faculty",
        admin: "/admin"
      };
      
      setTimeout(() => {
        window.location.href = routes[userType];
      }, 500);
    } catch (error: any) {
      toast.error(error.message || "Login failed. Please check your credentials.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (registerData.password !== registerData.confirm_password) {
      toast.error("Passwords do not match!");
      return;
    }
    
    if (registerData.password.length < 8) {
      toast.error("Password must be at least 8 characters!");
      return;
    }
    
    setIsLoading(true);
    
    try {
      const response = await authApi.register({
        first_name: registerData.first_name,
        last_name: registerData.last_name,
        email: registerData.email,
        password: registerData.password,
        phone: registerData.phone,
      });
      
      // Store token
      localStorage.setItem('token', response.token);
      localStorage.setItem('user', JSON.stringify(response.user));
      localStorage.setItem('role', response.role);
      
      toast.success(`Welcome, ${response.user.first_name}! Your account has been created.`);
      
      // Redirect to student dashboard
      setTimeout(() => {
        window.location.href = "/student";
      }, 500);
    } catch (error: any) {
      toast.error(error.message || "Registration failed. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-900 via-blue-800 to-teal-700">
      {/* Mobile Header - Only visible on small screens */}
      <div className="lg:hidden fixed top-0 left-0 right-0 z-50 bg-white/10 backdrop-blur-md border-b border-white/20">
        <div className="flex items-center justify-between px-4 py-3">
          <Link href="/" className="flex items-center gap-2 text-white">
            <div className="bg-white/20 p-1.5 rounded-lg">
              <GraduationCap className="h-5 w-5 text-white" />
            </div>
            <span className="font-bold text-lg">TClass</span>
          </Link>
          <Link href="/" className="text-sm text-blue-200 hover:text-white transition-colors">
            Back
          </Link>
        </div>
      </div>

      {/* Main Content */}
      <div className="min-h-screen flex items-center justify-center p-4 sm:p-6 pt-16 lg:pt-4">
        <div className="w-full max-w-6xl grid lg:grid-cols-2 gap-4 lg:gap-8">
          
          {/* Left Side - Info (Desktop Only) */}
          <div className="hidden lg:flex flex-col justify-center text-white px-4">
            <Link href="/" className="inline-flex items-center text-blue-200 hover:text-white mb-6 transition-colors w-fit">
              <ArrowLeft className="h-4 w-4 mr-2" />
              Back to Home
            </Link>
            
            <div className="flex items-center gap-4 mb-6">
              <div className="relative w-14 h-14 sm:w-16 sm:h-16">
                <Image
                  src="/tclass-logo.jpg"
                  alt="TCLASS Logo"
                  fill
                  className="object-contain rounded-full"
                  priority
                />
              </div>
              <div>
                <h1 className="text-xl sm:text-2xl font-bold">PGT - TCLASS</h1>
                <p className="text-blue-200 text-sm sm:text-base">Tarlac Center for Learning And Skills Success</p>
              </div>
            </div>

            <h2 className="text-3xl sm:text-4xl font-bold mb-4 leading-tight">
              Welcome to<br />
              <span className="text-yellow-400">TCLASS Portal</span>
            </h2>
            
            <p className="text-blue-100 text-base sm:text-lg mb-6">
              Access your courses, track your progress, and manage your training 
              programs all in one place.
            </p>

            <div className="space-y-3">
              <div className="flex items-center gap-3 p-2 rounded-lg hover:bg-white/5 transition-colors">
                <div className="w-10 h-10 bg-white/10 rounded-lg flex items-center justify-center flex-shrink-0">
                  <Users className="h-5 w-5 text-yellow-400" />
                </div>
                <div>
                  <p className="font-medium text-sm sm:text-base">For Students</p>
                  <p className="text-xs sm:text-sm text-blue-200">View courses, submit assignments, track grades</p>
                </div>
              </div>
              <div className="flex items-center gap-3 p-2 rounded-lg hover:bg-white/5 transition-colors">
                <div className="w-10 h-10 bg-white/10 rounded-lg flex items-center justify-center flex-shrink-0">
                  <Building2 className="h-5 w-5 text-yellow-400" />
                </div>
                <div>
                  <p className="font-medium text-sm sm:text-base">For Faculty</p>
                  <p className="text-xs sm:text-sm text-blue-200">Manage classes, grade students, create assignments</p>
                </div>
              </div>
              <div className="flex items-center gap-3 p-2 rounded-lg hover:bg-white/5 transition-colors">
                <div className="w-10 h-10 bg-white/10 rounded-lg flex items-center justify-center flex-shrink-0">
                  <Lock className="h-5 w-5 text-yellow-400" />
                </div>
                <div>
                  <p className="font-medium text-sm sm:text-base">For Admin</p>
                  <p className="text-xs sm:text-sm text-blue-200">System management and oversight</p>
                </div>
              </div>
            </div>
          </div>

          {/* Right Side - Login/Register Forms */}
          <div className="flex items-center justify-center px-0 sm:px-2">
            <Card className="shadow-2xl w-full max-w-md">
              <CardHeader className="text-center pb-4 sm:pb-6">
                {/* Logo - Mobile Only */}
                <div className="lg:hidden flex flex-col items-center mb-4">
                  <div className="relative w-14 h-14 sm:w-16 sm:h-16 mb-2">
                    <Image
                      src="/tclass-logo.jpg"
                      alt="TCLASS Logo"
                      fill
                      className="object-contain rounded-full"
                      priority
                    />
                  </div>
                  <p className="text-xs text-slate-500">Tarlac Center for Learning And Skills Success</p>
                </div>
                
                <CardTitle className="text-xl sm:text-2xl">Welcome Back</CardTitle>
                <CardDescription className="text-sm sm:text-base">
                  Sign in to your account or create a new one
                </CardDescription>
              </CardHeader>
              
              <CardContent className="pt-0">
                <Tabs defaultValue="login" className="w-full">
                  <TabsList className="grid w-full grid-cols-2 mb-4 sm:mb-6 h-11 sm:h-12">
                    <TabsTrigger value="login" className="text-sm sm:text-base">Sign In</TabsTrigger>
                    <TabsTrigger value="register" className="text-sm sm:text-base">Register</TabsTrigger>
                  </TabsList>

                  {/* Login Tab */}
                  <TabsContent value="login" className="space-y-4">
                    {/* User Type Selection */}
                    <div className="grid grid-cols-3 gap-2 sm:gap-3 mb-4">
                      {[
                        { id: "student", label: "Student", icon: Users },
                        { id: "faculty", label: "Faculty", icon: Building2 },
                        { id: "admin", label: "Admin", icon: Lock },
                      ].map((type) => (
                        <button
                          key={type.id}
                          onClick={() => setUserType(type.id as typeof userType)}
                          className={`flex flex-col items-center gap-1.5 sm:gap-2 p-2 sm:p-3 rounded-lg border-2 transition-all min-h-[70px] sm:min-h-[80px] justify-center
                            active:scale-95 touch-manipulation
                            ${userType === type.id
                              ? "border-blue-600 bg-blue-50 text-blue-600"
                              : "border-slate-200 hover:border-slate-300 text-slate-600 bg-white"
                            }`}
                        >
                          <type.icon className="h-5 w-5 sm:h-6 sm:w-6" />
                          <span className="text-xs sm:text-sm font-medium">{type.label}</span>
                        </button>
                      ))}
                    </div>

                    <form onSubmit={handleLogin} className="space-y-3 sm:space-y-4">
                      <div className="space-y-1.5 sm:space-y-2">
                        <Label htmlFor="email" className="text-sm sm:text-base">Email</Label>
                        <div className="relative">
                          <Mail className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 sm:h-5 sm:w-5 text-slate-400" />
                          <Input 
                            id="email" 
                            type="email" 
                            placeholder={userType === "student" ? "student@tclass.ph" : userType === "faculty" ? "faculty@tclass.ph" : "admin@tclass.ph"}
                            className="pl-10 sm:pl-11 h-11 sm:h-12 text-sm sm:text-base"
                            required 
                            value={loginEmail}
                            onChange={(e) => setLoginEmail(e.target.value)}
                            disabled={isLoading}
                            autoComplete="email"
                          />
                        </div>
                      </div>

                      <div className="space-y-1.5 sm:space-y-2">
                        <Label htmlFor="password" className="text-sm sm:text-base">Password</Label>
                        <div className="relative">
                          <Lock className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 sm:h-5 sm:w-5 text-slate-400" />
                          <Input 
                            id="password" 
                            type={showPassword ? "text" : "password"}
                            placeholder="Enter your password"
                            className="pl-10 sm:pl-11 pr-11 sm:pr-12 h-11 sm:h-12 text-sm sm:text-base"
                            required 
                            value={loginPassword}
                            onChange={(e) => setLoginPassword(e.target.value)}
                            disabled={isLoading}
                            autoComplete="current-password"
                          />
                          <button
                            type="button"
                            onClick={() => setShowPassword(!showPassword)}
                            className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-400 hover:text-slate-600 p-1 touch-manipulation min-w-[44px] min-h-[44px] flex items-center justify-center"
                            aria-label={showPassword ? "Hide password" : "Show password"}
                          >
                            {showPassword ? <EyeOff className="h-4 w-4 sm:h-5 sm:w-5" /> : <Eye className="h-4 w-4 sm:h-5 sm:w-5" />}
                          </button>
                        </div>
                      </div>

                      <div className="flex items-center justify-between text-xs sm:text-sm">
                        <label className="flex items-center gap-2 cursor-pointer">
                          <input type="checkbox" className="rounded border-slate-300 w-4 h-4" />
                          <span className="text-slate-600">Remember me</span>
                        </label>
                        <button 
                          type="button" 
                          className="text-blue-600 hover:underline touch-manipulation" 
                          onClick={() => toast.success("Password reset link sent!")}
                        >
                          Forgot password?
                        </button>
                      </div>

                      <Button 
                        type="submit" 
                        className="w-full bg-blue-600 hover:bg-blue-700 h-11 sm:h-12 text-sm sm:text-base font-medium touch-manipulation active:scale-[0.98] transition-transform" 
                        disabled={isLoading}
                      >
                        {isLoading ? "Signing in..." : `Sign In as ${userType.charAt(0).toUpperCase() + userType.slice(1)}`}
                      </Button>
                    </form>

                    {/* Social Login */}
                    <div className="mt-4 sm:mt-6">
                      <div className="relative">
                        <div className="absolute inset-0 flex items-center">
                          <div className="w-full border-t border-slate-200" />
                        </div>
                        <div className="relative flex justify-center text-xs sm:text-sm">
                          <span className="px-2 bg-white text-slate-500">Or continue with</span>
                        </div>
                      </div>

                      <div className="mt-3 sm:mt-4 grid grid-cols-2 gap-2 sm:gap-3">
                        <Button 
                          variant="outline" 
                          className="w-full h-10 sm:h-11 text-xs sm:text-sm touch-manipulation active:scale-[0.98] transition-transform" 
                          onClick={() => toast.success("Google login coming soon!")}
                        >
                          <svg className="h-4 w-4 sm:h-5 sm:w-5 mr-1.5 sm:mr-2" viewBox="0 0 24 24">
                            <path d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" fill="#4285F4"/>
                            <path d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" fill="#34A853"/>
                            <path d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" fill="#FBBC05"/>
                            <path d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" fill="#EA4335"/>
                          </svg>
                          <span className="hidden sm:inline">Google</span>
                          <span className="sm:hidden">Google</span>
                        </Button>
                        <Button 
                          variant="outline" 
                          className="w-full h-10 sm:h-11 text-xs sm:text-sm touch-manipulation active:scale-[0.98] transition-transform" 
                          onClick={() => toast.success("Facebook login coming soon!")}
                        >
                          <Facebook className="h-4 w-4 sm:h-5 sm:w-5 mr-1.5 sm:mr-2" />
                          <span className="hidden sm:inline">Facebook</span>
                          <span className="sm:hidden">Facebook</span>
                        </Button>
                      </div>
                    </div>
                  </TabsContent>

                  {/* Register Tab */}
                  <TabsContent value="register">
                    <form onSubmit={handleRegister} className="space-y-3 sm:space-y-4">
                      <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 sm:gap-4">
                        <div className="space-y-1.5 sm:space-y-2">
                          <Label htmlFor="first_name" className="text-sm sm:text-base">First Name</Label>
                          <Input 
                            id="first_name" 
                            placeholder="Juan"
                            required 
                            value={registerData.first_name}
                            onChange={(e) => setRegisterData({...registerData, first_name: e.target.value})}
                            disabled={isLoading}
                            className="h-11 sm:h-12 text-sm sm:text-base"
                            autoComplete="given-name"
                          />
                        </div>
                        <div className="space-y-1.5 sm:space-y-2">
                          <Label htmlFor="last_name" className="text-sm sm:text-base">Last Name</Label>
                          <Input 
                            id="last_name" 
                            placeholder="Dela Cruz"
                            required 
                            value={registerData.last_name}
                            onChange={(e) => setRegisterData({...registerData, last_name: e.target.value})}
                            disabled={isLoading}
                            className="h-11 sm:h-12 text-sm sm:text-base"
                            autoComplete="family-name"
                          />
                        </div>
                      </div>

                      <div className="space-y-1.5 sm:space-y-2">
                        <Label htmlFor="reg_email" className="text-sm sm:text-base">Email</Label>
                        <div className="relative">
                          <Mail className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 sm:h-5 sm:w-5 text-slate-400" />
                          <Input 
                            id="reg_email" 
                            type="email" 
                            placeholder="your@email.com"
                            className="pl-10 sm:pl-11 h-11 sm:h-12 text-sm sm:text-base"
                            required 
                            value={registerData.email}
                            onChange={(e) => setRegisterData({...registerData, email: e.target.value})}
                            disabled={isLoading}
                            autoComplete="email"
                          />
                        </div>
                      </div>

                      <div className="space-y-1.5 sm:space-y-2">
                        <Label htmlFor="reg_phone" className="text-sm sm:text-base">Phone Number</Label>
                        <div className="relative">
                          <User className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 sm:h-5 sm:w-5 text-slate-400" />
                          <Input 
                            id="reg_phone" 
                            type="tel"
                            placeholder="0917XXXXXXX"
                            className="pl-10 sm:pl-11 h-11 sm:h-12 text-sm sm:text-base"
                            value={registerData.phone}
                            onChange={(e) => setRegisterData({...registerData, phone: e.target.value})}
                            disabled={isLoading}
                            autoComplete="tel"
                          />
                        </div>
                      </div>

                      <div className="space-y-1.5 sm:space-y-2">
                        <Label htmlFor="reg_password" className="text-sm sm:text-base">Password</Label>
                        <div className="relative">
                          <Lock className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 sm:h-5 sm:w-5 text-slate-400" />
                          <Input 
                            id="reg_password" 
                            type={showPassword ? "text" : "password"}
                            placeholder="Min. 8 characters"
                            className="pl-10 sm:pl-11 pr-11 sm:pr-12 h-11 sm:h-12 text-sm sm:text-base"
                            required 
                            minLength={8}
                            value={registerData.password}
                            onChange={(e) => setRegisterData({...registerData, password: e.target.value})}
                            disabled={isLoading}
                            autoComplete="new-password"
                          />
                          <button
                            type="button"
                            onClick={() => setShowPassword(!showPassword)}
                            className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-400 hover:text-slate-600 p-1 touch-manipulation min-w-[44px] min-h-[44px] flex items-center justify-center"
                            aria-label={showPassword ? "Hide password" : "Show password"}
                          >
                            {showPassword ? <EyeOff className="h-4 w-4 sm:h-5 sm:w-5" /> : <Eye className="h-4 w-4 sm:h-5 sm:w-5" />}
                          </button>
                        </div>
                      </div>

                      <div className="space-y-1.5 sm:space-y-2">
                        <Label htmlFor="confirm_password" className="text-sm sm:text-base">Confirm Password</Label>
                        <div className="relative">
                          <Lock className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 sm:h-5 sm:w-5 text-slate-400" />
                          <Input 
                            id="confirm_password" 
                            type={showPassword ? "text" : "password"}
                            placeholder="Confirm your password"
                            className="pl-10 sm:pl-11 h-11 sm:h-12 text-sm sm:text-base"
                            required 
                            value={registerData.confirm_password}
                            onChange={(e) => setRegisterData({...registerData, confirm_password: e.target.value})}
                            disabled={isLoading}
                            autoComplete="new-password"
                          />
                        </div>
                      </div>

                      <div className="flex items-start gap-2 py-1">
                        <input 
                          type="checkbox" 
                          id="terms" 
                          required 
                          className="rounded border-slate-300 w-4 h-4 mt-0.5 flex-shrink-0" 
                        />
                        <label htmlFor="terms" className="text-xs sm:text-sm text-slate-600 leading-relaxed">
                          I agree to the <Link href="#" className="text-blue-600 hover:underline">Terms of Service</Link> and <Link href="#" className="text-blue-600 hover:underline">Privacy Policy</Link>
                        </label>
                      </div>

                      <Button 
                        type="submit" 
                        className="w-full bg-blue-600 hover:bg-blue-700 h-11 sm:h-12 text-sm sm:text-base font-medium touch-manipulation active:scale-[0.98] transition-transform" 
                        disabled={isLoading}
                      >
                        {isLoading ? "Creating Account..." : "Create Student Account"}
                      </Button>
                    </form>

                    <p className="mt-4 text-center text-xs sm:text-sm text-slate-600">
                      Already have an account?{' '}
                      <button 
                        className="text-blue-600 hover:underline font-medium touch-manipulation"
                        onClick={() => document.querySelector('[value="login"]')?.dispatchEvent(new Event('click'))}
                      >
                        Sign in
                      </button>
                    </p>
                  </TabsContent>
                </Tabs>

                {/* Mobile Back Link */}
                <div className="mt-4 pt-4 border-t text-center lg:hidden">
                  <Link href="/" className="inline-flex items-center text-xs sm:text-sm text-slate-500 hover:text-slate-700">
                    <ArrowLeft className="h-3 w-3 sm:h-4 sm:w-4 mr-1" />
                    Back to Home
                  </Link>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
}
